.floatingdiv {
    display: flex;
    justify-content: space-around;
    box-shadow: var(--boxShadow);
    border-radius: 17px;
    align-items: center;
    padding: 0px 28px 0px 0px;
    height: 4.5rem;
    background: #fff;
}

.floatingdiv>img {
    transform: scale(0.4);
}

.floatingdiv>span{
    font-size: 16px;
    font-family: sans-serif;
    color: black;
}